import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  ViewChild,
  ViewContainerRef
} from '@angular/core';
import { IOperation, IOperationBulk } from '@c8y/client';
import { gettext, Tab } from '@c8y/ngx-components';
import { BulkOperationModalDetailsService } from '@c8y/ngx-components/operations/bulk-operation-modal-details';

@Component({
  selector: 'c8y-operation-details-tabs',
  templateUrl: './operation-details-tabs.component.html'
})
export class OperationDetailsTabsComponent implements OnInit {
  @Input()
  operation: Partial<IOperation & IOperationBulk>;
  @Input()
  readOnly: boolean = false;

  @Input()
  bulkOperationModalDetailsService: BulkOperationModalDetailsService;
  @Output() onRetryFailedOperations: EventEmitter<Partial<IOperationBulk>> = new EventEmitter();
  @Output() showFailedOperation: EventEmitter<number> = new EventEmitter();

  @ViewChild('details', { static: true }) detailsTemplate: any;
  @ViewChild('audit', { static: true }) auditTemplate: any;

  tabs: Tab[] = [];
  template: ViewContainerRef;

  ngOnInit(): void {
    this.template = this.detailsTemplate;

    this.tabs = [
      {
        label: gettext('Details'),
        path: null,
        icon: 'asterisk',
        template: this.detailsTemplate
      },
      {
        label: gettext('History of changes'),
        path: null,
        icon: 'archive',
        template: this.auditTemplate
      }
    ];
  }
}
