import { Component, EventEmitter, Input, Output } from '@angular/core';
import { IManagedObject, IOperation, IOperationBulk } from '@c8y/client';
import { BulkOperationModalDetailsService } from '@c8y/ngx-components/operations/bulk-operation-modal-details';

@Component({
  selector: 'c8y-operation-details',
  templateUrl: './operation-details.component.html'
})
export class OperationDetailsComponent {
  @Input()
  operation: Partial<IOperation & IOperationBulk>;

  @Input()
  bulkOperationModalDetailsService: BulkOperationModalDetailsService;
  @Output() showFailedOperation: EventEmitter<number> = new EventEmitter();

  scrollToRetriedFromOperation(failedOperationId) {
    this.showFailedOperation.emit(failedOperationId);
  }

  openBulkOperationDetails(bulkOperationId) {
    this.bulkOperationModalDetailsService.openDetails(bulkOperationId);
  }
}
